:root {
  --primary: #2563EB;
  --secondary: #1E40AF;
  --accent: #EAB308;
  --background: #F8FAFC;
  --text: #1F2937;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: var(--background);
  color: var(--text);
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Plus Jakarta Sans', sans-serif;
}

/* Efectos visuales */
.bg-radial-gradient {
  background: radial-gradient(
    circle at center,
    transparent 0%,
    rgba(0, 0, 0, 0.2) 50%,
    rgba(0, 0, 0, 0.5) 100%
  );
}

/* Animaciones para el video de fondo y contenido */
@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fade-in {
  animation: fadeIn 0.8s ease-out forwards;
}

/* Optimizaciones para dispositivos móviles */
@media (max-width: 768px) {
  .video-optimized {
    transform: scale(1.2);
    transform-origin: center center;
  }
  
  .animate-fade-in {
    animation-duration: 0.6s; /* Animaciones más rápidas en móvil */
  }
}

/* Optimizaciones de rendimiento */
.performance-improvements {
  backface-visibility: hidden;
  perspective: 1000;
  transform-style: preserve-3d;
  will-change: transform, opacity;
}

/* Mejoras en la carga progresiva */
.progressive-load {
  transition: opacity 0.5s ease-out;
  opacity: 0;
}

.progressive-load.loaded {
  opacity: 1;
}

.animate-gradient {
  background-size: 200% 200%;
  animation: gradientAnimation 3s ease infinite;
}

/* Optimizaciones de rendimiento para video */
.video-optimized {
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  perspective: 1000;
  will-change: transform, opacity;
  
  /* Mejoras en el rendimiento del video */
  object-fit: cover;
  font-family: 'object-fit: cover'; /* Fallback para IE/Edge */
  
  /* Optimizaciones adicionales */
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
}

/* Estilos específicos para el video de fondo */
.video-container{
  position: relative;
  height: 100%;
  width: 100%;
  overflow: hidden;
  min-height: 100vh;
}

.video-background{
  position: absolute;
  inset: 0px;
  height: 100%;
  width: 100%;
  object-fit: cover;
  min-height: 100%;
  min-width: 100%;
  filter: brightness(0.8) contrast(1.1);
  transform-origin: center center;
  transition: opacity 1s ease-out, transform 0.3s ease-out;
  z-index: 0;
}

@media (max-width: 768px) {
  .video-background {
    transform: scale(1.2);
    object-position: center 20%;
  }
}

/* Overlay para el video */
.video-overlay{
  position: absolute;
  inset: 0px;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.75) 0%,
    rgba(0, 0, 0, 0.6) 50%,
    rgba(0, 0, 0, 0.75) 100%
  );
  mix-blend-mode: multiply;
  z-index: 1;
}

/* Contenido sobre el video */
.video-content{
  position: relative;
  z-index: 10;
}

/* Mejoras en la interactividad */
.interactive-element {
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
}

.interactive-element:active {
  transform: scale(0.98);
}

/* Animación de carga */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.animate-pulse-slow {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

.bg-texture {
  background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='1' cy='1' r='1' fill='%23FFFFFF' fill-opacity='0.1'/%3E%3C/svg%3E");
  background-size: 20px 20px;
}

/* Animaciones de fondo */
@keyframes gradientFlow {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.animate-gradient {
  background-size: 200% 200%;
  animation: gradientFlow 15s ease infinite;
}

/* Efectos de hover mejorados */
.hover-lift {
  transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1),
              box-shadow 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.hover-lift:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

/* Efectos de transición suaves */
.smooth-transition {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Efectos de carga */
@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

.loading-shimmer {
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0.1) 25%,
    rgba(255, 255, 255, 0.2) 50%,
    rgba(255, 255, 255, 0.1) 75%
  );
  background-size: 200% 100%;
  animation: shimmer 2s infinite;
}

/* Animaciones y transiciones */
.hover-scale{
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
}

.hover-scale:hover {
  transform: scale(1.05);
}

